




































import { Component, Vue  } from 'vue-property-decorator';
import Global from '../modules/Global';

@Component({})
export default class AboutPage extends Vue {

    private author = Global.authorInfo;

    private info = {
        tip: '这是一个前端技术博客',
        contentList: [
            {
                text: '为什么要写一个这样的博客，可能是我太闲了吧？？？也有可能是想把学到技术分享给一些有需要的人，记得当初学前端的时候基本就是百度，相信很多人也是。直到后来遇到一个学历很高的同事，他让我看一本书《JavaScript高级程序》，我花了两个星期很认真的看完了之后（边看边写代码）发现原来代码可以这样写，因为先前我也是一个jQuery小子，所以受jQuery编程思维影响的我首次感到JS的有趣性。然后就经常去CodePen这类网站去看别人的代码，再到之后自己开始尝试用原生去实现一个简单的jQuery库、编写各种常用的组件、JS到TS。'
            }, {
                text: '之后回头看看百度上面的那些转载了很多次的技术文章，比如我搜索一个vue computed 的使用，搜到文章的内容是直接复制官网的，我不明白为什么那些作者会有如此睿智的操作，如果官网能看懂还用来看你这篇文章？不单只是这种行为，还有百度上面能搜到的，大部分都是大家复制粘贴，转来转去的内容，基本上搜10篇文章有一半内容是相同的，最主要的是，对问题的解决没有得到有用的解决。也没有一个权威的网站可以定义或者说明这些问题的网站（api 官网这个不算），不过程序不就是这样么？'
            }, {
